/* stylelint-disable plugin/selector-bem-pattern */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: inherit; }

img {
  max-width: 100%;
  height: auto; }

button {
  cursor: pointer; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

body {
  position: relative;
  min-width: 330px;
  margin: 0;
  padding: 0;
  font: normal 400 18px/1.3 "Roboto", "Arial", sans-serif;
  color: #ffffff;
  background-color: #000000; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  position: relative;
  width: 320px;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .container {
      width: 387px; } }

.success,
.error {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  padding-top: 150px;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.8); }

.error {
  background-color: rgba(255, 0, 0, 0.8); }

.success__message,
.error__message {
  position: relative;
  margin: 0 20px;
  font: normal 400 60px/1.2 "Roboto", "Arial", sans-serif;
  color: #ffffff; }
  @media (max-width: 767px) {
    .success__message,
    .error__message {
      font: normal 400 36px/1.2 "Roboto", "Arial", sans-serif; } }

.contact {
  position: relative;
  min-height: 100vh;
  padding: 50px 0 100px;
  font: normal 400 18px/1.3 "Roboto", "Arial", sans-serif;
  color: #ffffff;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), top 0 center/1920px 1080px repeat url("../img/cosmos.jpg"), #000000; }

.contact__title {
  margin: 28px auto 25px;
  font: normal 500 55px/1.3 "Roboto", "Arial", sans-serif;
  color: #ffffff;
  text-align: center;
  overflow: hidden; }
  @media (max-width: 767px) {
    .contact__title {
      margin: 3px auto 21px;
      font: normal 500 44px/1.3 "Roboto", "Arial", sans-serif; } }

.contact__form-wrapper {
  position: relative;
  padding: 20px 0;
  border-radius: 27px;
  background-color: #171929;
  box-shadow: 0 4px 53px 5px rgba(0, 0, 0, 0.55);
  overflow-x: hidden;
  animation: zoom-in 0.5s cubic-bezier(0.445, 0.05, 0.55, 0.95); }

@keyframes zoom-in {
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3); }
  50% {
    opacity: 1; } }

.contact__inner-container {
  position: relative;
  margin: 0 45px;
  text-align: center; }
  @media (max-width: 767px) {
    .contact__inner-container {
      margin: 0 35px; } }

.contact__text {
  margin: 12px 20px 0;
  font: normal 400 18px/1.3 "Roboto", "Arial", sans-serif;
  color: #ffffff;
  text-align: center; }
  .contact__text--dark {
    color: #797ea3; }
  .contact__text--small {
    margin-top: 7px;
    font: normal 300 15px/1.3 "Roboto", "Arial", sans-serif; }
  @media (max-width: 767px) {
    .contact__text {
      margin: 6px 20px 0;
      font: normal 400 14.5px/1.3 "Roboto", "Arial", sans-serif; }
      .contact__text--small {
        font: normal 300 13px/1.3 "Roboto", "Arial", sans-serif; } }

.contact__fieldset {
  margin-top: 8px;
  text-align: left; }
  @media (max-width: 767px) {
    .contact__fieldset {
      margin-top: 3px; } }

.contact__item {
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 32px; }
  .contact__item:last-child {
    margin-bottom: 20px; }
  @media (max-width: 767px) {
    .contact__item {
      margin-bottom: 23px; }
      .contact__item:last-child {
        margin-bottom: 10px; } }

.contact__label {
  display: block;
  order: 1;
  font: normal 300 15px/1.3 "Roboto", "Arial", sans-serif;
  color: #797ea3; }
  .contact__label--req::after {
    content: "*";
    display: inline;
    font: normal 300 15px/1.3 "Roboto", "Arial", sans-serif;
    color: #797ea3;
    text-align: left; }
  @media (max-width: 767px) {
    .contact__label {
      font: normal 300 13px/1.3 "Roboto", "Arial", sans-serif; } }

.contact__input {
  display: block;
  order: 2;
  width: 100%;
  margin: 5px 0;
  padding: 4px 10px 1px 0;
  font: normal 400 18px/1.3 "Roboto", "Arial", sans-serif;
  color: #fefeff;
  text-align: left;
  border: none;
  border-bottom: 1px solid #3f4363;
  background-color: transparent; }
  .contact__input:hover, .contact__input:focus {
    border-bottom: 1px solid #ffffff; }
  .contact__input:hover + .contact__label,
  .contact__input:focus + .contact__label {
    color: #fefeff; }
  @media (max-width: 767px) {
    .contact__input {
      padding: 2px 30px 1px 0;
      font: normal 400 14.5px/1.3 "Roboto", "Arial", sans-serif; } }

.contact__policy-wrapper {
  display: flex;
  flex-flow: row wrap; }

.contact__checkbox-label {
  transition: transform 0.2s ease-in;
  position: relative;
  display: block;
  width: 31px;
  height: 31px;
  margin: 0 7px 0 2px;
  border-radius: 8px;
  background-color: #2c2f47;
  cursor: pointer; }
  .contact__checkbox-label:hover {
    transform: scale3d(0.9, 0.9, 0.9); }
  .contact__checkbox-label:active {
    transform: scale3d(0.8, 0.8, 0.8); }
  @media (max-width: 767px) {
    .contact__checkbox-label {
      width: 25px;
      height: 25px; } }

.contact__checkbox-input:checked + .contact__checkbox-label {
  background: center/16px 13px no-repeat url("../img/check.svg"), #2c2f47; }
  @media (max-width: 767px) {
    .contact__checkbox-input:checked + .contact__checkbox-label {
      background: center/13px 10px no-repeat url("../img/check.svg"), #2c2f47; } }

/* stylelint-disable */
.contact__checkbox-input:focus + .contact__checkbox-label {
  outline: 1px dotted #212121;
  outline: 5px auto -webkit-focus-ring-color; }

/* stylelint-enable */
.contact__policy-text {
  margin: 5px;
  padding: 0;
  font: normal 300 15px/1.3 "Roboto", "Arial", sans-serif;
  color: #797ea3;
  text-align: left; }
  @media (max-width: 767px) {
    .contact__policy-text {
      margin: 4px;
      font: normal 300 13px/1.3 "Roboto", "Arial", sans-serif; } }

.contact__police-link {
  color: #b9bee5;
  text-decoration: underline;
  transition: color 0.2s ease-in; }
  .contact__police-link:hover {
    color: #fefeff; }
  .contact__police-link:active {
    color: rgba(254, 254, 255, 0.7); }

.contact__button {
  transition: transform 0.2s ease-in;
  display: inline-block;
  margin: 30px 10px 20px;
  padding: 11px 40px 10px;
  font: normal 400 18px/1.3 "Roboto", "Arial", sans-serif;
  color: #fefeff;
  text-align: center;
  border: none;
  border-radius: 22px;
  background-color: #2c2f47; }
  .contact__button:hover {
    transform: scale3d(0.9, 0.9, 0.9); }
  .contact__button:active {
    transform: scale3d(0.8, 0.8, 0.8); }
  @media (max-width: 767px) {
    .contact__button {
      margin: 26px 10px 15px;
      padding: 10px 34px;
      font: normal 400 14.5px/1.3 "Roboto", "Arial", sans-serif;
      border-radius: 17px; } }
